<template>
  <div class="video-content-view">
    <div class="recomment">
      <div class="recomment-text">爆社推荐</div>
      <div class="recomment-change" @click="changeRecommendContent">
        <van-icon name="replay" /> 换一换
      </div>
    </div>
    <div class="top-content">
      <div
        class="content-list-wrap"
        v-for="(item, index) in topContentData"
        :key="index"
      >
        <UserMsgCom :contentData="item" @followHandel="followHandel(item)" />
      </div>
    </div>
    <div class="all" @click="goToAllBomb">
      <van-icon name="arrow" /><span class="arrow"
        ><van-icon name="arrow" /></span
      >全部爆社
    </div>
    <div class="content-box">
      <div
        class="content-list-wrap"
        v-for="(item, index) in contentData"
        :key="item.id"
      >
        <VideoCom
          :allContentData="contentData"
          :scrollTop="scrollTop"
          :contentData="item"
          :isAutoplay="index == 0 ? true : item.isAutoplay"
          :loading="loading"
          :isFollow="item.isFollow"
          :isDislike="item.isDislike"
          :isLike="item.isLike"
          :index="index"
          @getVideoHeight="getVideoHeight($event)"
          @getFollowState="getFollowState(item)"
          @getLikeState="getLikeState(item)"
          @getDislikeState="getDislikeState(item)"
          @addShareCount="addShareCount(item)"
          @deleteData="deleteData(item)"
          @goTocomment="goTocomment(item)"
        />
      </div>
    </div>
    <div class="refresh-icon" @click="refresh"></div>
  </div>
</template>

<script>
import { getHoneBombRecommendContent, getHoneBombContent } from "@/api/home";
import VideoCom from "@/components/HomeCom/VideoCom.vue";
import UserMsgCom from "@/components/HomeCom/UserMsgCom.vue";

export default {
  name: "BombSquadView",
  props: {
    scrollTop: {},
  },
  components: {
    VideoCom,
    UserMsgCom,
  },
  data() {
    return {
      //顶部内容数据
      topContentData: [],
      //内容数据
      contentData: [],
      //总高度
      videoTotalHeight: 0,
      //加载状态
      loading: true,
      //刷新状态
      refreshState: false,
      //所有容器的单个高度
      videoHeightArr: [],
      //获取顶部数据的id
      topDataId: 0,
      //获取数据的id
      dataId: 0,
      //刷新时获取的数据
      refreshData: [],
      //定时器延迟拿取数据
      timer: null,
    };
  },
  created() {
    this.getHoneBombRecommendContentData(this.topDataId);
    this.getHoneBombContentData(this.dataId);
  },
  activated() {
    //定时器延迟拿取数据
    this.timer = setTimeout(() => {
      this.getFollowedData();
      this.getFollowedData2();
      this.timer = null;
    }, 100);
  },
  watch: {
    scrollTop(newval, oldval) {
      let videoBottom = null;
      if (this.videoTotalHeight > newval) {
        videoBottom = this.videoTotalHeight - newval;
      }
      // console.log(newval);
      if (this.contentData.length > 0) {
        let autoplayIndex = Math.round(
          ((newval - 100 > 0 ? newval - 100 : 0) / this.videoTotalHeight) * 10
        );
        this.contentData.forEach((item, index) => {
          if (autoplayIndex == index) {
            item.isAutoplay = true;
          } else {
            item.isAutoplay = false;
          }
        });
      }
      if (
        (videoBottom > 0 && videoBottom < 500 && newval > oldval) ||
        videoBottom == null
      ) {
        if (this.loading) {
          this.loading = false;
          this.getHoneBombContentData(1);
        } else if (!this.loading && newval >= this.videoTotalHeight) {
          this.$toast({
            message: "没有更多了哦~",
            position: "bottom",
          });
        }
      }
      // console.log(this.contentData);
    },
    loading(newval) {
      if (newval == false) {
        this.videoTotalHeight = 0;
      }
    },
    "contentData.length"() {
      this.timer = setTimeout(() => {
        this.getFollowedData();
        this.timer = null;
      }, 100);
    },
    topContentData() {
      this.getFollowedData2();
    },
  },
  methods: {
    //获取数据
    async getHoneBombContentData(dataParams) {
      await getHoneBombContent({ axios: this.$axios }, dataParams).then(
        ({ data }) => {
          // console.log(data);
          data.data.forEach((item) => {
            item["isAutoplay"] = false;
            item["isFollow"] = false;
            item["isLike"] = false;
            item["isDislike"] = false;
          });
          this.contentData.push(...data.data);
          // console.log(this.contentData);
          let searchData = JSON.parse(
            window.localStorage.getItem("searchData")
          );
          if (!searchData) {
            window.localStorage.setItem(
              "searchData",
              JSON.stringify(this.contentData)
            );
          } else {
            let id = [];
            searchData.forEach((item) => {
              if (id.indexOf(item.id) == -1) {
                id.push(item.id);
              }
            });
            this.contentData.forEach((item) => {
              if(id.indexOf(item.id)==-1){
                searchData.push(item);
              }
            });
            window.localStorage.setItem(
              "searchData",
              JSON.stringify(searchData)
            );
          }
        }
      );
    },
    //获取报社推荐数据
    async getHoneBombRecommendContentData(dataParams) {
      await getHoneBombRecommendContent(
        { axios: this.$axios },
        dataParams
      ).then(({ data }) => {
        // console.log(data);
        data.data.forEach((item) => {
          item["isFollow"] = false;
        });
        this.topContentData = data.data;
        // console.log(this.topContentData);
      });
    },
    //获取所有内容的高度
    getVideoHeight(val) {
      this.videoTotalHeight += val;
    },
    //爆社推荐关注
    followHandel(item) {
      item.isFollow = !item.isFollow;
      let followedData2 = JSON.parse(
        window.localStorage.getItem("followedData2")
      );
      let idArr = [];
      followedData2.forEach((item) => {
        idArr.push(item.id);
      });
      if (item.isFollow && idArr.indexOf(item.id) == -1) {
        followedData2.push(item);
        window.localStorage.setItem(
          "followedData2",
          JSON.stringify(followedData2)
        );
      } else {
        // console.log(followedData2.indexOf(item));
        followedData2.forEach((item2, index) => {
          if (item2.id == item.id) {
            followedData2.splice(index, 1);
            window.localStorage.setItem(
              "followedData2",
              JSON.stringify(followedData2)
            );
          }
        });
      }
    },
    //内容关注
    getFollowState(item) {
      item.isFollow = !item.isFollow;
      let followedData = JSON.parse(
        window.localStorage.getItem("followedData")
      );
      let idArr = [];
      followedData.forEach((item) => {
        idArr.push(item.id);
      });
      if (item.isFollow && idArr.indexOf(item.user.id) == -1) {
        followedData.push(item.user);
        window.localStorage.setItem(
          "followedData",
          JSON.stringify(followedData)
        );
      } else {
        // console.log(followedData.indexOf(item.user));
        followedData.forEach((item2, index) => {
          if (item2.id == item.user.id) {
            followedData.splice(index, 1);
            window.localStorage.setItem(
              "followedData",
              JSON.stringify(followedData)
            );
          }
        });
      }
    },
    //点赞
    getLikeState(item) {
      item.isLike = !item.isLike;
      if (item.isLike) {
        item.isDislike = false;
        item.votes.up++;
      } else {
        item.votes.up--;
      }
    },
    //踩
    getDislikeState(item) {
      item.isDislike = !item.isDislike;
      if (item.isDislike && item.isLike) {
        item.isLike = false;
        item.votes.up--;
      }
    },
    //分享
    addShareCount(item) {
      item.share_count++;
    },
    //删除
    deleteData(item) {
      this.$dialog
        .confirm({
          title: "确定要删除吗?",
        })
        .then(() => {
          // on confirm
          this.contentData.forEach((item2, index) => {
            if (item2 == item) {
              this.contentData.splice(index, 1);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //更换爆社推荐内容
    changeRecommendContent() {
      this.topDataId++;
      if (this.topDataId > 2) {
        this.topDataId = 0;
      }
      this.getHoneBombRecommendContentData(this.topDataId);
      this.getFollowedData2();
    },
    //刷新内容
    refresh() {
      if (this.refreshState == false) {
        this.refreshState = true;
        this.dataId = 2;
        this.getHoneBombRecommendContentData(this.dataId);
        getHoneBombContent({ axios: this.$axios }, this.dataId).then(
          ({ data }) => {
            data.data.forEach((item) => {
              item["isAutoplay"] = false;
              item["isFollow"] = false;
              item["isLike"] = false;
              item["isDislike"] = false;
            });
            this.contentData.unshift(...data.data);
            console.log(this.contentData);
          }
        );
      } else {
        this.$toast({
          message: "没有更多啦~",
          position: "top",
        });
        this.videoHeightArr.unshift(...this.refreshData);
      }
    },
    //跳转路由到全部爆社页面
    goToAllBomb() {
      this.$router.push({ name: "allBomb" });
    },
    //获取已关注
    getFollowedData() {
      let followedData = JSON.parse(
        window.localStorage.getItem("followedData")
      );
      let followedDataId = [];
      followedData.forEach((item) => {
        followedDataId.push(item.id);
      });
      this.contentData.forEach((item) => {
        if (followedDataId.indexOf(item.user.id) != -1) {
          item.isFollow = true;
        } else {
          item.isFollow = false;
        }
      });
    },
    //获取爆社已关注
    getFollowedData2() {
      let followedData2 = JSON.parse(
        window.localStorage.getItem("followedData2")
      );
      let followedData2Id = [];
      followedData2.forEach((item) => {
        followedData2Id.push(item.id);
      });
      this.topContentData.forEach((item) => {
        if (followedData2Id.indexOf(item.id) != -1) {
          item.isFollow = true;
        } else {
          item.isFollow = false;
        }
      });
    },
    //跳转评论详情页
    goTocomment(item) {
      window.localStorage.setItem("commentContentData", JSON.stringify(item));
      let dataId = item.id;
      if (dataId != 125654473 && dataId != 125657703) {
        dataId = 125654473;
      }
      this.$router.push(`/comment?id=${dataId}`);
    },
  },
};
</script>

<style lang="scss" scoped>
.video-content-view {
  width: 100%;
  margin: 10px 0 65px;
  .recomment {
    width: 100%;
    display: flex;
    justify-content: space-between;
    color: #666;
    font-size: 14px;
  }
  .top-content {
    margin-top: 15px;
    width: 100%;
  }
  .all {
    margin: 15px 0;
    color: #666;
    border-bottom: 1px solid #ddd;
    padding-bottom: 20px;
    .arrow {
      margin-left: -5px;
    }
  }
  .content-box {
    width: 100%;
  }
  .refresh-icon {
    width: 35px;
    height: 35px;
    border-radius: 999px;
    position: absolute;
    bottom: 65px;
    right: 10px;
    background-image: url(../../assets/images/home/bYu.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    z-index: 99;
  }
}
</style>